.course-detail-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: 100vh;
}

.course-not-found {
  text-align: center;
  padding: 4rem 2rem;
}

.course-not-found h2 {
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.course-not-found p {
  color: var(--text-secondary);
}

.course-header {
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 3rem;
  margin-bottom: 3rem;
  align-items: start;
}

.course-thumbnail {
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 10px 30px var(--shadow-light);
}

.course-thumbnail img {
  width: 100%;
  height: 250px;
  object-fit: cover;
}

.play-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.play-overlay:hover {
  background: white;
  transform: translate(-50%, -50%) scale(1.1);
}

.play-icon {
  color: var(--primary-color);
  font-size: 1.5rem;
  margin-left: 4px;
}

.course-info h1 {
  font-size: 2.5rem;
  color: var(--text-primary);
  margin-bottom: 1rem;
  line-height: 1.2;
}

.course-description {
  font-size: 1.1rem;
  color: var(--text-secondary);
  margin-bottom: 2rem;
  line-height: 1.6;
}

.course-meta {
  display: flex;
  gap: 2rem;
  margin-bottom: 2rem;
}

.meta-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-secondary);
}

.meta-icon {
  color: var(--primary-color);
}

.course-actions {
  display: flex;
  gap: 1rem;
}

.btn-primary {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  min-width: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-size: 1rem;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px var(--shadow-glow);
}

.btn-primary:active {
  transform: translateY(0);
}

.btn-primary:disabled {
  cursor: not-allowed;
  opacity: 0.8;
}

.start-course-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  position: relative;
  overflow: hidden;
}

.start-course-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s;
}

.start-course-btn:hover::before {
  left: 100%;
}

.start-course-btn.loading {
  background: linear-gradient(135deg, #9ca3af 0%, #6b7280 100%);
}

.btn-icon {
  font-size: 1rem;
  transition: transform 0.3s ease;
}

.start-course-btn:hover .btn-icon {
  transform: scale(1.1);
}

.spinner {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.btn-secondary {
  background: transparent;
  color: var(--text-primary);
  border: 2px solid var(--border-color);
  padding: 1rem 2rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  min-width: 120px;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.btn-secondary:hover {
  border-color: var(--primary-color);
  color: var(--primary-color);
  transform: translateY(-1px);
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.2);
}

.btn-secondary:active {
  transform: translateY(0);
}

.btn-secondary.bookmarked {
  background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);
  border-color: #f59e0b;
  color: white;
}

.btn-secondary.bookmarked:hover {
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  border-color: #d97706;
  color: white;
}

.bookmarked-icon {
  color: #fbbf24;
  animation: bookmarkPulse 0.6s ease-in-out;
}

@keyframes bookmarkPulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

.course-content {
  display: grid;
  grid-template-columns: 1fr 300px;
  gap: 3rem;
}

.course-lessons h3 {
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

.lessons-list {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 4px 20px var(--shadow-light);
}

.lesson-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border-bottom: 1px solid var(--border-color);
  transition: all 0.3s ease;
}

.lesson-item:last-child {
  border-bottom: none;
}

.lesson-item:hover {
  background: var(--hover-bg);
  border-radius: 8px;
}

.lesson-number {
  width: 40px;
  height: 40px;
  background: var(--primary-color);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
}

.lesson-info h4 {
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

.lesson-info p {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.lesson-status {
  margin-left: auto;
}

.lesson-status .completed {
  color: var(--success-color);
  font-size: 1.2rem;
}

.lesson-status .pending {
  color: var(--text-secondary);
  font-size: 1.2rem;
}

.course-sidebar {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.instructor-info,
.course-stats {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 4px 20px var(--shadow-light);
}

.instructor-info h4,
.course-stats h4 {
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.instructor {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.instructor-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
}

.instructor-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.instructor-details h5 {
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

.instructor-details p {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.stat-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid var(--border-color);
}

.stat-item:last-child {
  border-bottom: none;
}

.stat-item span:first-child {
  color: var(--text-secondary);
}

.stat-item span:last-child {
  color: var(--text-primary);
  font-weight: 600;
}

@media (max-width: 768px) {
  .course-detail-container {
    padding: 1rem;
  }
  
  .course-header {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .course-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .course-info h1 {
    font-size: 2rem;
  }
  
  .course-meta {
    flex-direction: column;
    gap: 1rem;
  }
  
  .course-actions {
    flex-direction: column;
  }
}

[data-theme="dark"] .course-detail-container {
  background: var(--bg-primary);
  color: var(--text-primary);
}

[data-theme="dark"] .course-thumbnail {
  box-shadow: 0 10px 30px var(--shadow-medium);
}

[data-theme="dark"] .play-overlay {
  background: rgba(26, 26, 46, 0.8);
  color: var(--text-primary);
}

[data-theme="dark"] .course-info h1 {
  color: var(--text-primary);
}

[data-theme="dark"] .course-description {
  color: var(--text-secondary);
}

[data-theme="dark"] .course-meta-item {
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
  color: var(--text-primary);
}

[data-theme="dark"] .course-content {
  background: var(--glass-bg);
  border: 1px solid var(--border-color);
}

[data-theme="dark"] .content-tabs {
  background: rgba(26, 26, 46, 0.5);
  border: 1px solid rgba(139, 92, 246, 0.3);
}

[data-theme="dark"] .tab-button {
  color: var(--text-secondary);
}

[data-theme="dark"] .tab-button.active {
  background: var(--primary-color);
  color: var(--text-primary);
}

[data-theme="dark"] .lesson-item {
  background: rgba(26, 26, 46, 0.6);
  border: 1px solid rgba(139, 92, 246, 0.2);
}

[data-theme="dark"] .lesson-item:hover {
  background: var(--hover-bg);
  border-color: var(--border-accent);
}

[data-theme="dark"] .lesson-title {
  color: var(--text-primary);
}

[data-theme="dark"] .lesson-duration {
  color: var(--text-secondary);
}