.profile-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: 100vh;
}

.profile-header {
  display: flex;
  align-items: flex-start;
  gap: 3rem;
  margin-bottom: 3rem;
  background: var(--card-background);
  padding: 2.5rem;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.profile-avatar {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.profile-avatar img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid var(--primary-color);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  background: var(--card-background);
}

.profile-avatar img:hover {
  transform: scale(1.05);
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.2);
  border-color: var(--secondary-color);
}

.level-badge {
  position: absolute;
  bottom: -5px;
  right: -5px;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.profile-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 120px;
}

.profile-details h1 {
  font-size: 2.5rem;
  color: var(--text-primary);
  margin: 0 0 0.75rem 0;
  line-height: 1.2;
  font-weight: 700;
}

.email {
  color: var(--text-secondary);
  font-size: 1.1rem;
  margin: 0 0 0.5rem 0;
  font-weight: 500;
}

.join-date {
  color: var(--text-muted);
  margin: 0 0 1.5rem 0;
  font-size: 0.95rem;
}

.btn-edit {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.btn-edit:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.3);
}

.edit-form {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  width: 100%;
}

.edit-input {
  padding: 0.875rem 1rem;
  border: 2px solid var(--border-color);
  border-radius: 10px;
  font-size: 1rem;
  background: var(--input-background);
  color: var(--text-primary);
  transition: all 0.3s ease;
  font-weight: 500;
}

.edit-input:focus {
  outline: none;
  border-color: var(--primary-color);
}

.edit-actions {
  display: flex;
  gap: 1rem;
  margin-top: 0.5rem;
}

.btn-save {
  background: var(--success-color);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.btn-save:hover {
  background: #059669;
  transform: translateY(-2px);
}

.btn-cancel {
  background: var(--error-color);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.btn-cancel:hover {
  background: #dc2626;
  transform: translateY(-2px);
}

.profile-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.stat-card {
  background: var(--card-background);
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  gap: 1.5rem;
  transition: all 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.stat-icon {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
}

.stat-info h3 {
  font-size: 2rem;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

.stat-info p {
  color: var(--text-secondary);
  font-weight: 500;
}

.profile-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: start;
}

.badges-section h2,
.achievements-section h2 {
  color: var(--text-primary);
  margin: 0 0 2rem 0;
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1.3;
}

.badges-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1.5rem;
}

.badge-card {
  background: var(--card-bg);
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: var(--shadow-card);
  border: 1px solid var(--border-color);
  text-align: center;
  transition: all 0.3s ease;
}

.badge-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-floating);
  border-color: var(--border-accent);
  background: var(--hover-bg);
}

.badge-card:hover h4 {
  color: var(--primary-color);
  transition: color 0.3s ease;
}

.badge-card:hover p {
  color: var(--text-primary);
  transition: color 0.3s ease;
}

.badge-card:hover .badge-icon {
  transform: scale(1.1);
  transition: transform 0.3s ease;
}

.badge-card:hover .badge-rarity {
  transform: scale(1.05);
  transition: transform 0.3s ease;
}

.badge-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.badge-card h4 {
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.badge-card p {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.badge-rarity {
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
}

.badge-rarity.common {
  background: rgba(107, 114, 128, 0.1);
  color: var(--text-secondary);
  border: 1px solid rgba(107, 114, 128, 0.2);
}

.badge-rarity.uncommon {
  background: rgba(59, 130, 246, 0.1);
  color: #3b82f6;
  border: 1px solid rgba(59, 130, 246, 0.2);
}

.badge-rarity.rare {
  background: rgba(245, 158, 11, 0.1);
  color: #f59e0b;
  border: 1px solid rgba(245, 158, 11, 0.2);
}

.badge-rarity.epic {
  background: rgba(139, 92, 246, 0.1);
  color: #8b5cf6;
  border: 1px solid rgba(139, 92, 246, 0.2);
}

[data-theme="dark"] .badge-rarity.common {
  background: rgba(107, 114, 128, 0.2);
  color: #9ca3af;
}

[data-theme="dark"] .badge-rarity.uncommon {
  background: rgba(59, 130, 246, 0.2);
  color: #60a5fa;
}

[data-theme="dark"] .badge-rarity.rare {
  background: rgba(245, 158, 11, 0.2);
  color: #fbbf24;
}

[data-theme="dark"] .badge-rarity.epic {
  background: rgba(139, 92, 246, 0.2);
  color: #a78bfa;
}

.achievements-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.achievement-item {
  background: var(--card-bg);
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: var(--shadow-card);
  border: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  gap: 1rem;
  transition: all 0.3s ease;
}

.achievement-item:hover {
  transform: translateX(4px);
  box-shadow: var(--shadow-floating);
  border-color: var(--border-accent);
  background: var(--hover-bg);
}

.achievement-item:hover .achievement-info h4 {
  color: var(--primary-color);
  transition: color 0.3s ease;
}

.achievement-item:hover .achievement-info p {
  color: var(--text-primary);
  transition: color 0.3s ease;
}

.achievement-item:hover .achievement-date {
  color: var(--text-primary);
  transition: color 0.3s ease;
}

.achievement-item:hover .achievement-icon {
  transform: scale(1.1);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
  transition: all 0.3s ease;
}

.achievement-item:hover .achievement-xp {
  transform: scale(1.05);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
  transition: all 0.3s ease;
}

.achievement-icon {
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.2rem;
}

.achievement-info {
  flex: 1;
}

.achievement-info h4 {
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

.achievement-info p {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 0.25rem;
}

.achievement-date {
  color: var(--text-secondary);
  font-size: 0.8rem;
}

.achievement-xp {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-weight: 600;
  font-size: 0.9rem;
}

@media (max-width: 768px) {
  .profile-container {
    padding: 1rem;
  }
  
  .profile-header {
    flex-direction: column;
    text-align: center;
    gap: 2rem;
    padding: 2rem;
  }
  
  .profile-info {
    min-height: auto;
  }
  
  .profile-details h1 {
    font-size: 2rem;
  }
  
  .profile-stats {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .profile-content {
    grid-template-columns: 1fr;
    gap: 2.5rem;
  }
  
  .badges-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
  
  .edit-actions {
    flex-direction: column;
  }
}